import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
export default {
	data() {
		return {
			// curSelectIndex:0
		}
	},
	computed: {
		...mapState(['questionOriginData', 'answer_index']),
		curSelectIndex: function() {
			// 当前题目选择的答案序号
			return this.questionOriginData[this.answer_index-1].selectIndex
		}
	},
	mounted(){

	},
	methods:{
		...mapMutations(['setQuestionOriginData']),
		openMater: function() {
			this.$material ({
				click: () => {
					this.$router.push('……')
				}
			})
		},
		openLogin: function() {
			this.$login ({
				click: () => {
					this.$router.push('……')
				}
			})
		},
		single_select_btn(index, aIndex = this.answer_index) {
			// aIndex 问题序号
			// index 答案序号
			const me =this;
			let stage_data = me.questionOriginData[aIndex-1].btns;
			for (let i = 0; i < stage_data.length; i++) {
				me.$set(stage_data[i], 'select', false)
			}
			me.$set(stage_data[index], 'select', true)
			me.setQuestionOriginData([stage_data,index])
		},
	}
}